import { IProjectManagementEarnedValueCalculatorResult } from "../../interfaces/IProjectManagementEarnedValueCalculatorResult";
import { IDataDisplayMaterial } from "../interfaces/IDataDisplayMaterial";

class TableUtils {
  public getDataDisplayMaterial(
    key: keyof IProjectManagementEarnedValueCalculatorResult,
    value: number
  ): IDataDisplayMaterial {
    switch (key) {
      case "plannendValue":
        return {
          name: "\n(PV)",
          popconfirm: {
            title: "(Plannend Value, PV)",
            description: "=  * ",
          },
        };
      case "earnedValue":
        return {
          name: "\n(EV)",
          popconfirm: {
            title: "(Earned Value, EV)",
            description: "=  * ",
          },
        };
      case "actualCost":
        return {
          name: "\n(AC)",
          popconfirm: {
            title: "(Actual Cost, AC)",
            description: "=  * ",
          },
        };
      case "scheduleVariance":
        return {
          name: "\n(SV)",
          tag:
            value > 0
              ? {
                  color: "green",
                  text: "",
                }
              : {
                  color: "red",
                  text: "",
                },
          popconfirm: {
            title: "(Schedule Variance, SV)",
            description: "= EV - PV",
          },
        };
      case "costVariance":
        return {
          name: "\n(CV)",
          tag:
            value > 0
              ? {
                  color: "green",
                  text: "",
                }
              : {
                  color: "red",
                  text: "",
                },
          popconfirm: {
            title: "(Cost Variance, CV)",
            description: "= EV - AC",
          },
        };
      case "schedulePerformedIndex":
        return {
          name: "\n(SPI)",
          tag:
            value > 1
              ? {
                  color: "green",
                  text: "",
                }
              : {
                  color: "red",
                  text: "",
                },
          popconfirm: {
            title: "(Schedule Performed Index, SPI)",
            description: "= EV / PV",
          },
        };
      case "costPerformedIndex":
        return {
          name: "\n(CPI)",
          tag:
            value > 1
              ? {
                  color: "green",
                  text: "",
                }
              : {
                  color: "red",
                  text: "",
                },
          popconfirm: {
            title: "(Cost Performed Index, CPI)",
            description: "= EV / AC",
          },
        };
      case "budgetAtCompletion":
        return {
          name: "\n(BAC)",
          popconfirm: {
            title: "(Budget At Completion, BAC)",
            description: "= ",
          },
        };
      case "estimateAtCompletion":
        return {
          name: "\n(EAC)",
          popconfirm: {
            title: "(Estimate At Completion, EAC)",
            description: "= BAC / CPI",
          },
        };
      case "estimateToCompletion":
        return {
          name: "\n(ETC)",
          popconfirm: {
            title: "(Estimate To Completion, ETC)",
            description: "= EAC - AC",
          },
        };
      case "varianceAtCompletion":
        return {
          name: "\n(VAC)",
          popconfirm: {
            title: "(Variance At Completion, VAC)",
            description: "= BAC - EAC",
          },
        };
      case "done":
        return {
          name: "",
          popconfirm: {
            title: "",
            description: "= EV / BAC",
          },
        };
      case "fcst":
        return {
          name: "",
          popconfirm: {
            title: "",
            description: "= BAC - EV",
          },
        };
      default:
        const _expandCheck: never = key;
        return _expandCheck;
    }
  }
}
export const tableUtils = new TableUtils();
